import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {FuseConfigService} from "../../core/services/config.service";
import {AuthService} from "../../core/services/auth.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  regiserForm: FormGroup;
  forgotPasswordFormErrors: any;

  constructor(
    private fuseConfig: FuseConfigService,
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private router: Router
  )
  {
    this.fuseConfig.setSettings({
      layout: {
        navigation: 'none',
        toolbar   : 'none',
        footer    : 'none'
      }
    });

    this.forgotPasswordFormErrors = {
      email: {}
    };
  }

  ngOnInit()
  {
    this.regiserForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required]
    });

    // this.regiserForm.valueChanges.subscribe(() => {
    //   this.onForgotPasswordFormValuesChanged();
    // });
  }

  signup(){
    this.authService.signup(this.regiserForm.controls.email.value, this.regiserForm.controls.password.value).subscribe((data) => {
      console.log(data);

      this.router.navigate(['/login']);
    });
  }



  onForgotPasswordFormValuesChanged()
  {
    for ( const field in this.forgotPasswordFormErrors )
    {
      if ( !this.forgotPasswordFormErrors.hasOwnProperty(field) )
      {
        continue;
      }

      // Clear previous errors
      this.forgotPasswordFormErrors[field] = {};

      // Get the control
    //   const control = this.forgotPasswordForm.get(field);
    //
    //   if ( control && control.dirty && !control.valid )
    //   {
    //     this.forgotPasswordFormErrors[field] = control.errors;
    //   }
    }
  }
}




