import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {url} from "../../../environments/environment";

@Injectable()
export class AuthService {
  user;
  constructor(private http: HttpClient) { }

  login(email, password){
    return this.http.post(url + 'auth/login', {	"email": email, "password": password});
  }

  signup(email, password){
    return this.http.post(url + 'auth/register', {	"email": email, "password": password});
  }

  forgotpassword(email){
    return this.http.post(url + 'auth/forgotpassword', {	"email": email});
  }

  getUser(){
    return this.user;
  }

  setUser(user){
    this.user = user;
  }

}



